import React from "react";
import styled from 'styled-components';
import { Parallax } from "react-parallax";
import Fade from 'react-reveal/Fade';
import Grid from '@mui/material/Grid';
import Footer from "../components/Footer";
import Slider from "react-slick";
import BannerImg from '../assets/homepage/parallax-banner-2.jpg';
import RoofImg from '../assets/homepage/solar-roof.jpg';
import PergolaImg from '../assets/homepage/solar-pergola.jpg';
import PanelImg from '../assets/homepage/panel.jpg';
import BannerVideo from "../assets/homepage/home-banner.mp4";
import ReliabilityVideo from '../assets/homepage/reliability_short.mp4'
import Roof1 from '../assets/homepage/roof-img-1.jpg';
import Roof2 from '../assets/homepage/roof-img-2.jpg';
import Roof3 from '../assets/homepage/roof-img-3.jpg';
import Roof4 from '../assets/homepage/roof-img-4.jpg';
import Pergola1 from '../assets/homepage/pergola-img-1.jpg';
import Pergola2 from '../assets/homepage/pergola-img-2.jpg';
import Pergola3 from '../assets/homepage/pergola-img-3.jpg';
// import { Parallax as ParallaxOuter, ParallaxLayer } from '@react-spring/parallax'
// import HammerTile from "../assets/homepage/hammer-tile.mp4";
// import HammerPanel from "../assets/homepage/hammer-panel.mp4";
// import CoconutTile from "../assets/homepage/coconut-tile.mp4";
// import CoconutPanel from "../assets/homepage/coconut-panel.mp4";
// import BallTile from "../assets/homepage/ball-tile.mp4";
// import BallPanel from "../assets/homepage/ball-panel.mp4";
import Navbar from "../components/Navbar";
// import { animateScroll as scroll } from 'react-scroll';
import ScrollToTopIcon from '../components/ScrollToTopIcon';
import { LazyLoadComponent, LazyLoadImage } from 'react-lazy-load-image-component';
import HubspotForm from '../components/HubspotForm';
// import Sundial from "../components/Sundial";
import axios from 'axios';
import ReactMarkdown from 'react-markdown';

class Homepage extends React.Component {
  state = {
    scroll: false,
    bottom: false,
    content: {}
  }
  _isMounted = false
  componentDidMount() {
    this._isMounted = true;
    axios.get('https://arkacms.xyz/api/homepage-contents')
      .then(res => {
        // console.log(res.data);
        let data = [...res.data.data];
        let content = {};
        data.forEach(item => {
          content[item?.attributes?.label] = item?.attributes?.richContent || item?.attributes?.content;
        });
        this.setState({ content: { ...content } });
        // console.log(content)
      })
      .catch(err => {
        console.log('Error : ', err)
      });
    // scroll.scrollToTop({
    //   duration: 400,
    //   smooth: 'easeInOut'
    // });
    // window.addEventListener("scroll", this.onScroll, false);
  }
  onScroll = (e) => {
    let that = this;
    var scroll = window.scrollY;
    if (scroll > 100 && that._isMounted) that.setState({ scroll: true });
    else if (that._isMounted) that.setState({ scroll: false });
    if (((window.innerHeight + window.pageYOffset + 50) >= document.body.offsetHeight) && that._isMounted)
      that.setState({ bottom: true })
    else if (that._isMounted)
      that.setState({ bottom: false })
  }
  componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener('scroll', this.onScroll, false);
  }
  rootRef = React.createRef();
  bannerRef = React.createRef();
  roofRef = React.createRef();
  pergolaRef = React.createRef();
  panelRef = React.createRef();
  exploreRef = React.createRef();
  render() {
    let content = this.state.content;
    return (
      <Wrapper ref={this.rootRef}>

        {/* <Sundial root={this.props.containerRef} refs={[this.bannerRef, this.roofRef, this.pergolaRef, this.panelRef, this.exploreRef]} /> */}

        <Navbar containerRef={this.rootRef} />

        <Parallax
          bgImage={null}
          bgImageAlt="solar roof"
          strength={300}
          renderLayer={percentage => (
            <div ref={this.bannerRef} style={{ minHeight: "150vh", background: 'rgba(0,0,0,0.4)', position: 'relative' }}>
              <LazyLoadComponent>
                <HeroVideo poster={BannerImg} loop playsInline autoPlay muted>
                  <source src={BannerVideo} type="video/mp4" />
                </HeroVideo>
              </LazyLoadComponent>
              <Banner style={{ marginTop: `${(percentage - 1) * 45}%` }}>
                <Fade left duration={1000}>
                  <FlexContainer style={{ width: '22rem', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '4rem', margin: '2rem 0' }}>{content?.heroText}</FlexContainer>
                </Fade>
                <Fade opposite bottom delay={500} duration={500}>
                  <FlexContainer style={{ width: '30rem', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '1.75rem', lineHeight: '2' }}>{content?.heroSubtext}</FlexContainer>
                </Fade>
                <Fade opposite delay={1000} duration={1000}>
                  <PaddedText>
                    <YOButton>
                      {content?.heroButtonText}
                    </YOButton>
                  </PaddedText>
                </Fade>
              </Banner>
            </div>
          )}
        >
        </Parallax>


        <LazyLoadComponent>
          <Parallax
            bgImage={BannerImg}
            bgImageStyle={{ opacity: '0', zIndex: '-1' }}
            bgImageAlt="solar roof"
            strength={-300}
            renderLayer={percentage => {
              return (
                <div style={{ minHeight: "100vh", zIndex: '100', padding: '10vh 5vw', background: "#001020" }}>
                  <Grid container ref={this.roofRef} style={{ color: 'white', marginTop: `${(percentage - 0.6) * 45}vh`, flexWrap: 'wrap-reverse' }}>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                      <FlexContainer>
                        <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                          <Fade bottom delay={250} duration={250}>
                            {content?.sectionOneHead}
                          </Fade>
                        </PaddedText>
                        <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                          <Fade bottom delay={500} duration={250}>
                            <div>
                              <ReactMarkdown escapeHTML={false} >
                                {content?.sectionOneContent}
                              </ReactMarkdown>
                            </div>
                          </Fade>
                        </PaddedText>
                        <PaddedText style={{ width: '90%', display: 'flex' }}>
                          <Fade bottom delay={750} duration={250}>
                            <BWButton>
                              {content?.sectionOneButtonText}
                            </BWButton>
                          </Fade>
                          <Fade bottom delay={850} duration={250}>
                            <YOButton style={{ marginLeft: '2rem' }}>
                              Get Yours
                            </YOButton>
                          </Fade>
                        </PaddedText>
                      </FlexContainer>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                      <PaddedText style={{ padding: '3rem 0' }}>
                        <Fade bottom delay={500} duration={250}>
                          <ImageContainer src={RoofImg} />
                        </Fade>
                      </PaddedText>
                    </Grid>
                  </Grid>
                </div>
              );
            }}
          >
          </Parallax >
        </LazyLoadComponent>

        <LazyLoadComponent>
          <Parallax
            bgImage={BannerImg}
            bgImageStyle={{ opacity: '0', zIndex: '-1' }}
            bgImageAlt="solar roof"
            strength={-300}
            renderLayer={percentage => {
              return (
                <div style={{ minHeight: "100vh", zIndex: '100', padding: '10vh 5vw', background: "#002040" }}>
                  <Grid ref={this.pergolaRef} container style={{ color: 'white', marginTop: `${(percentage - 0.6) * 45}vh`, flexWrap: 'wrap-reverse' }}>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                      <FlexContainer>
                        <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                          <Fade bottom delay={250} duration={250}>
                            {content?.sectionTwoHead}
                          </Fade>
                        </PaddedText>
                        <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                          <Fade bottom delay={500} duration={250}>
                            <ReactMarkdown escapeHTML={false} >
                              {content?.sectionTwoContent}
                            </ReactMarkdown>
                          </Fade>
                        </PaddedText>
                        <PaddedText style={{ width: '90%', display: 'flex' }}>
                          <Fade bottom delay={750} duration={250}>
                            <BWButton>
                              {content?.sectionTwoButtonText}
                            </BWButton>
                          </Fade>
                          <Fade bottom delay={850} duration={250}>
                            <YOButton style={{ marginLeft: '2rem' }}>
                              Get Yours
                            </YOButton>
                          </Fade>
                        </PaddedText>
                      </FlexContainer>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                      <PaddedText style={{ padding: '3rem 0' }}>
                        <Fade bottom delay={500} duration={250}>
                          <ImageContainer src={PergolaImg} />
                        </Fade>
                      </PaddedText>
                    </Grid>
                  </Grid>
                </div>
              );
            }}
          >
          </Parallax >
        </LazyLoadComponent>

        <Parallax
          bgImage={null}
          bgImageStyle={{ zIndex: '-1', minHeight: '120vh' }}
          bgImageAlt="solar roof"
          strength={100}
          renderLayer={percentage => {
            return (
              <div ref={this.panelRef} style={{ minHeight: "50vh", zIndex: '100', display: 'flex', alignItems: 'center', justifyContent: 'center', background: "#001020" }}>
                <center>
                  <LazyLoadImage src={PanelImg} effect="blur" alt='Solar Panel' style={{ maxWidth: '90%', margin: '5vh auto' }}>
                    {/* <img src={PanelImg} style={{ width: '80%' }} alt="Solar Panel" /> */}
                  </LazyLoadImage>
                </center>
              </div>
            );
          }}
        >
        </Parallax >

        <HubspotForm />

        <Parallax
          bgImage={BannerImg}
          bgImageStyle={{ opacity: '0', zIndex: '-1' }}
          bgImageAlt="solar roof"
          strength={-300}
          renderLayer={percentage => {
            return (
              <div style={{ minHeight: "100vh", zIndex: '100', padding: '10vh 5vw', background: "#001020" }}>
                <Grid ref={this.pergolaRef} container style={{ color: 'white', marginTop: `${(percentage - 1) * 45}vh`, flexWrap: 'wrap-reverse' }}>
                  <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                    <FlexContainer>
                      <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                        <Fade bottom delay={250} duration={250}>
                          {content?.galleryPowerRoofHead}
                        </Fade>
                      </PaddedText>
                      <Slider
                        dots={true}
                        infinite={true}
                        speed={500}
                        slidesToShow={1}
                        slidesToScroll={1}
                        autoplay={true}
                        autoplaySpeed={4000}
                        pauseOnHover={false}
                        touchMove={true}
                        swipeToSlide={true}
                        style={{ width: '90%' }}
                      >
                        <LazyLoadComponent><CarouselImg src={Roof1} /></LazyLoadComponent>
                        <LazyLoadComponent><CarouselImg src={Roof2} /></LazyLoadComponent>
                        <LazyLoadComponent><CarouselImg src={Roof3} /></LazyLoadComponent>
                        <LazyLoadComponent><CarouselImg src={Roof4} /></LazyLoadComponent>
                      </Slider>
                    </FlexContainer>
                  </Grid>
                  <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
                    <FlexContainer>
                      <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                        <Fade bottom delay={250} duration={250}>
                          {content.galleryPowerPergolaHead}
                        </Fade>
                      </PaddedText>
                      <Slider
                        dots={true}
                        infinite={true}
                        speed={500}
                        slidesToShow={1}
                        slidesToScroll={1}
                        autoplay={true}
                        autoplaySpeed={4000}
                        pauseOnHover={false}
                        touchMove={true}
                        swipeToSlide={true}
                        style={{ width: '90%' }}
                      >
                        <LazyLoadComponent><CarouselImg src={Pergola1} /></LazyLoadComponent>
                        <LazyLoadComponent><CarouselImg src={Pergola2} /></LazyLoadComponent>
                        <LazyLoadComponent><CarouselImg src={Pergola3} /></LazyLoadComponent>
                      </Slider>
                    </FlexContainer>
                  </Grid>
                </Grid>
              </div>
            );
          }}
        >
        </Parallax >

        <Parallax
          bgImage={BannerImg}
          bgImageStyle={{ opacity: '0', zIndex: '-1' }}
          bgImageAlt="solar roof"
          strength={-300}
          renderLayer={percentage => {
            return (
              <div style={{ minHeight: "100vh", zIndex: '100', padding: '10vh 0', background: "#002040" }}>
                <Grid container style={{ color: 'white', marginTop: `${(percentage - 1) * 45}vh` }}>
                  <Grid item xs={12} xl={12}>
                    <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%', color: 'white', marginLeft: '5vw' }}>
                      {content.sectionFourHead}
                    </PaddedText>
                  </Grid>
                  <Grid container item xs={12} xl={12}>
                    <LazyLoadComponent><Video controls src={ReliabilityVideo} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                  </Grid>

                  {/* <Grid container item xs={12} xl={12}>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={BallTile} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={BallPanel} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                  </Grid>
                  <hr style={{ height: '2px', width: '100%', margin: '2rem 0', background: 'white' }} />
                  <Grid container item xs={12} xl={12}>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={HammerTile} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={HammerPanel} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                  </Grid>
                  <hr style={{ height: '2px', width: '100%', margin: '2rem 0', background: 'white' }} />
                  <Grid container item xs={12} xl={12}>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={CoconutTile} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                    <Grid item xs={12} sm={6} md={6} lg={6} xl={6}>
                      <LazyLoadComponent><Video src={CoconutPanel} playsInline autoPlay loop muted></Video></LazyLoadComponent>
                    </Grid>
                  </Grid> */}
                </Grid >
              </div >
            );
          }
          }
        >
        </Parallax >

        <Footer rf={this.exploreRef} />

        <ScrollToTopIcon scroll={this.state.scroll} bottom={this.state.bottom} />
      </Wrapper >
    );
  }
}

export default Homepage;

const Wrapper = styled.div`
  width: 100%;
`;

const Banner = styled.div`
  padding: 3rem 5vw;
  position: absolute;
  width: 90vw;
  top: 35%;
  left: 50%;
  transform: translate(-50%,-35%);
  color: white;
`;

const HeroVideo = styled.video`
  width: auto;
  height: 150vh;
  z-index: -1;
  mix-blend-mode: multiply;
  position: absolute;
  
  @media only screen and (max-width : 1260px) {
    left: -20vw;
  }  
  @media only screen and (max-width : 900px) {
    left: -60vw;
  }
  @media only screen and (max-width : 600px) {
    left: -200vw;
  }
`;

const Video = styled.video`
  width: 100%;
  height: auto;
`;
// const Video = styled.video`
//   width: 90%;
//   margin: 2% 5%;
//   height: auto;
// `;

const FlexContainer = styled.div`
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-direction: column;
`;

// const FullFlexContainer = styled.div`
//   display: flex;
//   align-items: center;
//   justify-content: center;
//   flex-direction: column;
//   width: 100%;
// `;

const PaddedText = styled.div`
  padding: 2rem 0;
  line-height: 2;
`;

const ImageContainer = styled.div`
  background: url(${props => props.src});
  margin:0 5%;
  width: 100%;
  height: 0;
  padding-top: 75%;
  background-size: cover;
  background-position: center;
`;

const CarouselImg = styled.div`
  background: url(${props => props.src});
  margin:0 0rem !important;
  width: 100%;
  height: 0;
  padding-top: 75%;
  background-size: cover;
  background-position: center;
`;

const BWButton = styled.button`
  background: #fff4;
  color: white;
  border: 2px solid white;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  z-index: 100;
  transition: all 0.3s ease;
  cursor: pointer;

  &:hover {
    background: white;
    color: #000;
  }
`;

const YOButton = styled.button`
  background: #0007;
  color: orange;
  border: 2px solid orange;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  z-index: 100;
  transition: all 0.3s ease;
  cursor: pointer;

  &:hover {
    background: orange;
    color: black;
  }
`;