import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import React, { Component } from 'react';
import { GoogleApiWrapper } from 'google-maps-react';
import styled from 'styled-components';
import MapContainer from './MapContainer';
import Grid from '@mui/material/Grid';
// import backImg from '../assets/homepage/calculator-background.jpg';
// import axios from 'axios';

class HubSpotForm extends Component {
	state = {
		address: '',
		firstName: "",
		lastName: "",
		email: "",
		phoneNo: "",
		validPlace: false,
		showMap: true,
		showSubmittedMessage: false,
		submitting: false,
		addressLatLng: {
			lat: null,
			lng: null
		}
	}
	placesInput = React.createRef();
	places = null
	geocoder = null;
	componentDidMount() {
		if (this.props.google) {
			const google = this.props.google;
			this.geocoder = new google.maps.Geocoder()
			this.places = new google.maps.places.Autocomplete(
				this.placesInput.current,
				{ componentRestrictions: { country: ['us', 'in'] } }
			);
			this.places.addListener('place_changed', this.handlePlaceChanged);
			// const google = this.props.google;
			this.places = new google.maps.places.Autocomplete(this.placesInput.current);
			this.places.addListener('place_changed', this.handlePlaceChanged);
		}
	}

	updateAddressFromMap = (coords) => {
		this.setState({ addressLatLng: coords })

		this.geocoder
			.geocode({ location: coords })
			.then((response) => {
				this.setState({ address: response.results[0].formatted_address, validPlace: true });
			})
			.catch((e) => console.log("Geocoder failed due to: " + e));

	}

	handlePlaceChanged = () => {
		let place = this.places.getPlace();
		if (place?.geometry) {
			this.setState({
				address: place.formatted_address,
				addressLatLng: {
					lat: place.geometry.location.lat(),
					lng: place.geometry.location.lng()
				},
				validPlace: true
			})
		}
	}

	getCookie = (name) => {
		var nameEQ = name + "=";
		var ca = document.cookie.split(';');
		for (var i = 0; i < ca.length; i++) {
			var c = ca[i];
			while (c.charAt(0) === ' ') c = c.substring(1, c.length);
			if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length, c.length);
		}
		return null;
	}

	submitContactForm = (e) => {
		console.log("Submitting")
		this.setState({ submitting: true })
		e.preventDefault();
		let fields = [
			{ "name": "firstname", "value": this.state.firstName },
			{ "name": "lastname", "value": this.state.lastName },
			{ "name": "email", "value": this.state.email },
			{ "name": "gmaps_address", "value": this.state.address },
			{ "name": "phone", "value": this.state.phoneNo },
			{ "name": "l2cstage", "value": 'UL' }
		]

		let context = {
			pageUri: "https://arka.energy",
			pageName: "Arka Marketing Website Contact Form"
		}

		let cookie = this.getCookie('hubspotutk')

		if (cookie) {
			context = {
				...context,
				hutk: cookie
			}
		}

		fetch('https://ipv4.jsonip.com').then(response => response.json()).then(json => json.ip ? context = { ...context, ipAddress: json.ip } : console.log('no ip')).then(json => {
			fetch(`https://api.hsforms.com/submissions/v3/integration/submit/${19982243}/${'a07419c3-5d5d-4aaf-8806-367accd10bd3'}`, {
				method: "POST",
				mode: "cors",
				cache: "no-cache",
				credentials: "omit",
				headers: {
					"Content-Type": "application/json; charset=utf-8",
				},
				body: JSON.stringify({
					fields: fields,
					context
				})
			})
				.then(response => response.text())
				.then(json => {
					console.log("Submitted");
					this.setState({ showSubmittedMessage: true, submitting: false }, () => {
						setTimeout(() => {
							this.setState({ showSubmittedMessage: false });
						}, 5000);
					})
				})
				.catch(error => {
					this.setState({ submitting: false });
					alert('Some error occured. Please try again later.');
					console.log(error)
				});
		})
	}


	render() {
		return (
			<Wrapper ref={this.props.rf} >
				<Inner>
					<Grid container>
						<Grid item xs={12} sm={12} md={7} lg={8} xl={8}>
							<TitleText>Contact Us</TitleText>
							<EstimateForm onKeyPress={(e) => { if (e.key === "Enter" || e.key === "Return") { e.stopPropagation(); e.preventDefault(); } }} onSubmit={this.submitContactForm}>
								<InlineInput style={{ marginRight: '2.5%' }}>
									<InputWrapper id='first-name' width='96%'>
										<TransparentInput
											value={this.state.firstName}
											// onInput={(e) => {
											// 	e.target.value = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');
											// }}
											onChange={(e) => {
												this.setState({ firstName: e.target.value })
											}}
											width='calc(100% - 4rem)'
											type='text'
											placeholder='First Name...'
										/>
									</InputWrapper>
								</InlineInput>
								<InlineInput style={{ marginLeft: '2.5%' }}>
									<InputWrapper id='last-name' width='96%'>
										<TransparentInput
											value={this.state.lastName}
											// onInput={(e) => {
											// 	e.target.value = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');
											// }}
											onChange={(e) => {
												this.setState({ lastName: e.target.value })
											}}
											width='calc(100% - 4rem)'
											type='text'
											placeholder='Last Name...'
										/>
									</InputWrapper>
								</InlineInput>
								<InlineInput style={{ marginRight: '2.5%' }}>
									<InputWrapper id='email' width='96%'>
										<TransparentInput
											value={this.state.email}
											// onInput={(e) => {
											// 	e.target.value = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');
											// }}
											onChange={(e) => {
												this.setState({ email: e.target.value })
											}}
											width='calc(100% - 4rem)'
											type='text'
											placeholder='Email...'
										/>
									</InputWrapper>
								</InlineInput>
								<InlineInput style={{ marginLeft: '2.5%' }}>
									<InputWrapper id='phone' width='96%'>
										<TransparentInput
											value={this.state.phoneNo}
											onInput={(e) => {
												e.target.value = e.target.value.replace(/[^0-9.]/g, '').replace(/(\..*?)\..*/g, '$1');
											}}
											onChange={(e) => {
												this.setState({ phoneNo: e.target.value })
											}}
											width='calc(100% - 4rem)'
											type='text'
											placeholder='Phone No ...'
										/>
									</InputWrapper>
								</InlineInput>
								<>
									<FullLengthInput>
										<InputWrapper id='places' width='96%'>
											<LocationOnIcon style={{ position: 'absolute', fontSize: '1.5rem', left: '0.5rem', top: '0.5rem' }} />
											<TransparentInput
												ref={this.placesInput}
												value={this.state.address}
												onBlur={(e) => { if (!this.state.validPlace) e.target.value = ''; this.setState({ address: '' }) }}
												onChange={(e) => { this.setState({ address: e.target.value, validPlace: false }) }}
												type='text'
												placeholder='Address...'
												style={{ marginLeft: '1rem' }}
											/>
										</InputWrapper>
									</FullLengthInput>
								</>
								<div style={{ display: 'flex', alignItems: 'center', padding: '1rem 0' }}>
									<SubmitButton disabled={this.state.submitting} submitting={this.state.submitting} type='submit'>
										{this.state.submitting ? 'Sending...' : 'Send'}
									</SubmitButton>
									<div style={{ transition: 'all 0.3s ease', opacity: (this.state.showSubmittedMessage ? '1' : '0'), fontSize: '0.75rem', padding: '0.5rem 1rem', backgroundColor: 'none', color: 'white', width: 'max-content', borderRadius: '1rem', display: 'inline-flex', alignItems: 'center' }}>
										<CheckCircleIcon style={{ marginRight: '1rem', color: 'green' }} /> Your request has been submitted.
									</div>
								</div>
							</EstimateForm>
						</Grid>
						<Grid item xs={12} sm={12} md={5} lg={4} xl={4} style={{ position: 'relative' }}>
							<MapWrapper>
								<MapContainer addressLatLng={this.state.addressLatLng} updateAddressFromMap={this.updateAddressFromMap} >
								</MapContainer>
							</MapWrapper>
						</Grid>
					</Grid>
				</Inner>
			</Wrapper>
		);
	}
}


export default GoogleApiWrapper({
	apiKey: 'AIzaSyCrboLVdRbgnPURqV8_1fRg0mSD7VxwEVs',

})(HubSpotForm);

